<template>
  <div class="login-container">
    <div class="login-box">
      <h3 class="login-title">小U商城后台管理系统</h3>
      <el-form
        :model="loginInfo"
        :rules="rules"
        ref="loginForm"
        class="demo-ruleForm"
      >
        <!-- prop: 和表单校验相关的属性  -->
        <el-form-item prop="username">
          <el-input type="text" placeholder="请输入用户名" v-model="loginInfo.username" autocomplete="off">
              <template slot="prepend">
                  <i class="el-icon-user-solid"></i>
              </template>
          </el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input :show-password="true" type="password"  placeholder="请输入密码"  v-model="loginInfo.password" autocomplete="off">
              <template slot="prepend">
                  <i class="el-icon-lock"></i>
              </template>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" class="btn-login" @click="submitForm">提交</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 和表单元素进行绑定的数据
      loginInfo: {
        username: 'admin',
        password: 'admin888'
      },
      // 表单校验规则
      rules: {
        // required表示数据必须填写
        // message: 错误提示信息
        // tigger: 触发表单校验的时间点
        username: [{ required:true,message:'请输入用户名',trigger:'blur' }],
        password: [{ required:true,message:'请输入密码',trigger:'blur' }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      // 表单校验方法
      this.$refs.loginForm.validate(valid => {
        // valid: 系统注入的参数, true代表表单校验通过, false代表表单校验不通过
        if (valid) {
          this.$http.post('/userlogin',this.loginInfo).then(res=>{
              console.log(res);
              if(res.code===200){
                 this.$message({
                  type: 'success',
                  message: res.msg
                });
                // 将登录信息存储到本地存储localStorage中
                localStorage.setItem('logininfo',JSON.stringify(res.list));
                // 跳转到管理中心
                this.$router.push('/')
              }else{
                this.$message({
                  type: 'error',
                  message: res.msg
                });
              }
          })
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: url("../assets/imgs/loginbg.jpg") no-repeat;
}
.login-box {
  width: 400px;
  height: 300px;
  background: #fff;
  border-radius: 4px;
  padding:0 30px;
}
.login-title {
  color: #409eff;
  text-align: center;
  margin: 40px 0;
}
.btn-login{
    width:100%;
}
</style>


